import { useContextState } from 'dynamic-context-provider'
import React from 'react'
import { ITEM_TYPES, useGetItems } from '../../common/hooks/useGetItems'
import { useGetTargets } from '../../common/hooks/useGetTargets'
import BaseForm from './BaseForm'

const ItemsForm = ({closeModal}) => {
    const { user } = useContextState()
    const [userItems]= useGetItems(user, ITEM_TYPES.misc)
    const [targets, loading] = useGetTargets()
    if(loading)return null
    return (
       <BaseForm user={user} targets={targets} options={userItems} onSubmit={closeModal} type="Items" />
    )
}

export default ItemsForm
